*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  font-family: "Roboto", sans-serif;
}

html {
  font-size: 12px;
  font-weight: 500;
}

body {
  margin: 0;
}

@import "header";
@import "card";

@media screen and (min-width: 1024px) {
  .card {
    flex-basis: 25%;
  }
}
